    box-sizing: border-box;
    font-size: 20px;
}

body {
    padding: 0;
    margin: 0;
}

.invisible {
    display: none;
}

.hidden {
    visibility: hidden;
}

/* MAIN DISPLAY*/
.container {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    border: 1px solid cyan;
    width: clamp(300px, 100vw, 1000px);
    height: 100vh;
    overflow: scroll;
}

.display-header {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    height: 100px;
    background-color: gainsboro;
}

.left-panel, .right-panel {
    width: 100%;
}

.add-button {
    filter: invert(100%) sepia(100%) saturate(10000%) hue-rotate(80deg);
}

.projects-button, .add-button {
    width: 20vw;
    height: 20vw;
}

/* PROJECT TILE */
.project-tile {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 15vh;
    width: 100%;
    border: 1px solid green;
}

.project-name {
    font-size: 1.25rem;
    overflow-wrap: break-word;
}

.edit-project {
    margin: 3vw;
}

.project-tile:hover {
    background-color: aquamarine;
}

/* TODO TILE */
.todo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px;
    border: 1px solid green;
}

.todo-text-container {
    width: 60%;
    overflow-wrap: break-word;
}

.todo-btns-container {
    display: flex;
    gap: 20px;
}

/* BUTTONS */
.edit-project, .delete-project, .dialog-close-button, .view-btn, .delete-btn, .is-done-btn, .edit-btn, .submit-button {
    width: 15vw;
    height: 15vw;
}

.edit-project:hover, .delete-project:hover, .dialog-close-button:hover, .view-btn:hover, .delete-btn:hover, .is-done-btn:hover, .edit-btn:hover, .submit-button:hover, .projects-button:hover, .add-button:hover {
    filter: drop-shadow(3px 3px 3px red);
}

/* DIALOG */
.dialog {
    position: relative;
    width: 98vw;
    height: 90vh;
    padding: 10px;
    border-radius: 10px;
}

.form-content {
    max-width: 90vw;
}

.dialog-close-container {
    text-align: right;
}

.dialog::backdrop {
    background: rgba(0, 0, 0, 0.85)
}

.counter {
    position: absolute;
    top: 10vw;
    left: 10vw;
    font-size: 1.25rem;
    color: goldenrod;
}

.overcap {
    color: red;
}

.submit-disabled {
    filter: invert(50%);
}

/* PROJECT DIALOG */
.isproject {
    /* width: 95vw; */
    height: 60vw;
    padding: 2vw 3vw;
}

.project-name-input {
    border: 1px solid red;
    padding: 5px 5px 5px 10px;
    border-radius: 10px;
    margin: 3vw auto;
}

.description-view {
    font-style: italic;
}

.btn-container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 5vw;
}

.new-project-submit {
    /* float: right; */
}

/* EDIT or NEW TODO DIALOG */
.todo-dialog-container {
    padding: 3vw;
}

.form-input {
    border: 1px solid red;
    border-radius: 10px;
    width: 100%;
    margin-bottom: 3vw;
    padding: 5px 0 5px 10px;
}

textarea.notes {
    height: 35vh;
    padding-right: 5px;
}

.new-todo-submit, .edit-todo-submit {
    float: right;
    margin-right: 3vw;
}

/* VIEW TODO DIALOG */
.todo-view-container {
    padding: 3vw;
}

.description-view {
    height: 15vh;
    border: 1px solid green;
}

.notes-view {
    height: 20vh;
    border: 1px solid green;
    overflow-wrap: break-word;
    overflow-y: scroll;
}

.description-view, .notes-view {
    padding: 5px;
}

.buttons-container {
    display: flex;
    justify-content: space-between;
}
